.number {
  display: inline-block;
  background-repeat: no-repeat;
  background-position: center center;
  width: 68px;
  height: 76px;
  text-align: center;
  line-height: 76px;
  overflow: hidden;
}
.number i,
.number span {
  font-size: 40px;
}
.number :global .icon-shuzi6,
.number :global .icon-shuzi7,
.number :global .icon-shuzi8 {
  position: relative;
  top: -2px;
}
.point {
  display: inline-block;
  background-repeat: no-repeat;
  text-align: center;
  overflow: hidden;
}
i {
  display: inline-block;
  margin: 5px 0;
  font-family: "digital-7";
}
:global .icon-shuzi6,
:global .icon-shuzi7,
:global .icon-shuzi8 {
  position: relative;
  top: -1px;
}
