.nowTime :global {
  /* CLOCK */
  font-family: "digital-7";
  /*END CLOCK*/
  /* AM OR PM*/
  /*CLASS THAT CHANGES COLOR OF TEXT TO APPEAR LIKE ITS "ON"*/
}
.nowTime :global div.clock {
  margin: 5px 0;
}
.nowTime :global div.clock div {
  display: inline-block;
  position: relative;
}
.nowTime :global div.clock div p {
  font-size: 100px;
  position: relative;
  z-index: 100;
  color: #00CFFF;
}
.nowTime :global div.clock .placeholder {
  color: rgba(255, 255, 255, 0.1);
  position: absolute;
  top: 0;
  z-index: 50;
}
.nowTime :global .am-pm {
  font-family: sans-serif;
  text-transform: uppercase;
  width: 20px;
}
.nowTime :global div.am-pm div p {
  font-size: 12px;
  font-weight: bold;
  width: 100%;
}
.nowTime :global .am,
.nowTime :global .pm {
  color: #131212;
}
.nowTime :global .light-on {
  color: #ffffff;
}
